/*---------------------------
  Fonts
----------------------------*/
/*--------------------------------------------------------------
#0.1    Theme Reset Style
--------------------------------------------------------------*/
@font-face {
  font-family: 'Cairo-Regular';
  src: url(/static/media/Cairo-Regular.e793edb5.ttf) format('ttf'), 
   url(/static/media/Cairo-Regular.e793edb5.ttf) format('truetype'); /* Chrome 4+, Firefox 3.5, Opera 10+, Safari 3—5 */
}  

body {
  font-family: 'Roboto', sans-serif!important;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  overflow-x: hidden;
  color:#333;
}
body:lang(ar){
  font-family: 'Cairo-Regular' !important;
}

@media (max-width: 767px) {
  body {
    font-size: 15px;
  }
}

p {
  color: #666;
  line-height: 30px;
  font-size:16px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Montserrat', sans-serif;
  color: #353535;
}

ul {
  list-style-type: none;
  padding-left: 0;
  margin: 0!important;
  margin-bottom: 0;
}

a {
  text-decoration: none;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

a:hover {
  text-decoration: none!important;
}

img {
  max-width: 100%;
}

button,
a:focus {
  outline: none;
}
.features-padding {
  padding: 100px 0;
}

@media (max-width: 991px) {
  .features-padding {
    padding: 90px 0;
  }
}

@media (max-width: 767px) {
  .features-padding {
    padding: 80px 0;
  }
}

.features-padding{
  padding: 100px 0;
}
.section-padding{
  padding: 100px 0;
}
.ptb-100-70{
  padding: 100px 0 70px;
}



.fixed-navbar {
  position: relative;
  z-index: 5555;
}
.fixed-navbar.active .header-style-1,
.fixed-navbar.active .header-style-2,
.fixed-navbar.active .header-style-3{
  position: fixed;
  z-index: 555;
  top: 0;
  left: 0;
  width: 100%;
  background: #274091;;
  border: none;
  border-bottom: 1px solid #c60874;

  box-shadow: 0px 1px 5px rgba(0, 0, 0, .1);
    -moz-box-shadow: 0px 1px 5px rgba(0, 0, 0, .1);
    -webkit-box-shadow: 0px 1px 5px rgba(0, 0, 0, .1);
    -webkit-transition: all 1s ease;
    transition: all 1s ease;
}
.fixed-navbar.active .header-style-2,
.fixed-navbar.active .header-style-3{
  background: #231b0e;
}
input:-internal-autofill-selected {background-color: white;background: white}

::-webkit-scrollbar {
  width: 15px;
  border-radius: 1px;
}
/* Handle */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #274091;
  border-radius: 1px;
}
::-webkit-scrollbar-thumb {
  background: #274091;
  border-radius: 1px;
}
::-webkit-scrollbar-thumb:hover {
  background: #c60874;
}
